import "./header.scss"
import React from 'react';
import Logo from "../logo/logo";
import {FaHamburger} from "react-icons/fa";
import ShopCartBadge from "../shop-cart-badge/shop-cart-badge";

import {Link} from "react-router-dom";
const Header = () => {
    return (
        <div className={"header"}>
            <div className="header__left-col">
               <div className="header__burger-icon">
                   <FaHamburger/>
               </div>
                <div className="header__branding">
                    <Link to={"/restaurant"} className={"header__link"}> <Logo title={"Food Time"}/></Link>
                </div>
            </div>
            <div className="header__right-col">
                <div className={"header__right-menu"}>
                    <div className="header__menu-item">
                        My account
                    </div>
                </div>
                <ShopCartBadge/>
            </div>
        </div>
    );
};

export default Header;